import React from "react";
import { wishList } from "./List";
import { Grid } from "../styled-components/Grid";
import { Img } from "../styled-components/Img";
import { Card, CardHeader } from "../styled-components/Card";
import { TitleSub } from "../styled-components/Titles";
import { Text } from "../styled-components/Text";
import { Link } from "../styled-components/Link";

export default function DisplayList() {
  return (
    <Grid>
      {wishList.map((wish) => {
        return (
          <Card key={wish.id}>
            <CardHeader>
              <TitleSub>{wish.name}</TitleSub>
            </CardHeader>
            <Img src={wish.picture} alt={wish.name} />
            <Link href={wish.link}>Länk</Link>
            <Text>{wish.notes}</Text>
          </Card>
        );
      })}
    </Grid>
  );
}
