import styled from "styled-components";

export const Title = styled.h1`
  font-family: "the_perfect_christmasregular";
  //font-family: 'merry_christmas_starregular';
  font-size: 130px;
  font-weight: normal;
  text-align: center;
  color: white;
  //word-wrap: normal;

  @media only screen and (max-width: 750px) {
    font-size: 100px;
  }

  @media only screen and (max-width: 600px) {
    font-size: 80px;
  }

  @media only screen and (max-width: 500px) {
    font-size: 60px;
  }
`;

export const TitleSub = styled.h2`
  font-family: "the_perfect_christmasregular";
  font-size: 60px;
  font-weight: normal;
  text-align: center;
  color: green;
`;
